<template>
    <div class="container">
        <DmgCalc/>
    </div>
</template>

<script>
/* eslint-disable */
import { ref, watch } from 'vue'
import { useLanguage } from '@/store/Language'
import { storeToRefs } from 'pinia'
import DmgCalc from '@/components/DmgCalc.vue'

export default {
    name: 'CalcView',
    components: {
        DmgCalc,
    },
    setup() {
        const { language } = storeToRefs(useLanguage())
        useLanguage().changeLanguage()
        watch(language, () => {
            useLanguage().changeLanguage()
        })
    }
}
</script>

