                                <input type="number" aria-label="enemy_RES" class="form-control input-atk" placeholder="15"
                                style="background: transparent; font-weight: bold;" 
                                v-model="enemy_RES" @input="calculation">
                                <span class="input-group-text">%</span>
                            </div>
                        </td>
                        <th scope="row">- {{ RES }}%</th>
                    </tr>
                    <tr>
                        <th scope="row">{{ text.pen }}</th>
                        <td>
                            <div class="input-group input-group-sm">
                                <input type="number" aria-label="unit_pen" class="form-control input-atk" placeholder="15"
                                style="background: transparent; font-weight: bold;" 
                                v-model="unit_pen" @input="calculation">
                                <span class="input-group-text">%</span>
                            </div>
                        </td>
                        <td>
                            <div class="input-group input-group-sm">
                                <input type="number" aria-label="enemy_pen" class="form-control input-def" placeholder="0"
                                style="background: transparent; font-weight: bold;" 
                                v-model="enemy_pen" @input="calculation">
                                <span class="input-group-text">%</span>
                            </div>
                        </td>
                        <th scope="row">+ {{ pen }}%</th>
                    </tr>
                    <tr>
                        <th scope="row" colspan="4">DMG = {{ DMG }}</th>
                    </tr>
                </tbody>
            </table>
        </div>
    </div>
    <div class="row" style="height: 30vh;"></div>
</template>

<script>
/* eslint-disable */
import { ref, watch, onUpdated, onMounted } from 'vue'
import { computed } from '@vue/runtime-core'
import { useMobile } from "@/store/Mobile"
import { useLanguage } from '@/store/Language'
import { storeToRefs } from 'pinia'
import Characters from '@/assets/json/Characters.json'

export default {
    name: 'DmgCalc',
    setup() {
        const { isMobile } = storeToRefs(useMobile())
        const { language, text } = storeToRefs(useLanguage())


        const unit_ATK = ref('')
        const unit_skill = ref('')
        const unit_ATK_p = ref('')
        const unit_ATK_f = ref('')
        const unit_cdmg = ref('')
        const unit_level = ref('')
        const unit_DEF = ref('')
        const unit_RES = ref('')
        const unit_pen = ref('')
        const enemy_ATK = ref('')
        const enemy_skill = ref('')
        const enemy_ATK_p = ref('')
        const enemy_ATK_f = ref('')
        const enemy_cdmg = ref('')
        const enemy_level = ref('')
        const enemy_DEF = ref('')
        const enemy_RES = ref('')
        const enemy_pen = ref('')
        const ATK = ref('')
        const skill = ref('')
        const ATK_p = ref('')
        const ATK_f = ref('')
        const element = ref('')
        const cdmg = ref('')
        const level = ref('')
        const DEF = ref('')
        const RES = ref('')
        const pen = ref('')
        const DMG = ref(0)
        const trueDMG = ref(false)
        const ele_res = ref(false)
        
        const atking = ref(true)

        const isElement = () => {
            if (atking.value) {
                if ((elementBtns.value[0] && elementBtns_e.value[1]) ||
                    (elementBtns.value[1] && elementBtns_e.value[2]) ||
                    (elementBtns.value[2] && elementBtns_e.value[0]) ||
                    (elementBtns.value[3] && elementBtns_e.value[4]) ||
                    (elementBtns.value[4] && elementBtns_e.value[3])) {
                    return true
                }
            }else {
                if ((elementBtns_e.value[0] && elementBtns.value[1]) ||
                    (elementBtns_e.value[1] && elementBtns.value[2]) ||
                    (elementBtns_e.value[2] && elementBtns.value[0]) ||
                    (elementBtns_e.value[3] && elementBtns.value[4]) ||
                    (elementBtns_e.value[4] && elementBtns.value[3])) {
                    return true
                }
            }
            return false
        }

        const leveling = () => {
            if (atking.value) {
                if (enemy_level.value - unit_level.value > 4) {
                    return Math.min((enemy_level.value - unit_level.value) * 2, 50)
                }
            }else {
                if (unit_level.value - enemy_level.value > 4) {
                    return Math.min((unit_level.value - enemy_level.value) * 2, 50)
                }
            }
            return 0
        }

        const defing = () => {
            if (atking.value) {
                if (enemy_DEF.value) {
                    return Math.floor((enemy_DEF.value) / (enemy_DEF.value + 25 * unit_level.value + 500) * 10000) / 100
                }
            }else {
                if (unit_DEF.value) {
                    return Math.floor((unit_DEF.value) / (unit_DEF.value + 25 * enemy_level.value + 500) * 10000) / 100
                }
            }
            return 0
        }

        const calculation = () => {
            ATK.value = (atking.value ? unit_ATK.value : enemy_ATK.value)
            if (ATK.value == '') {
                ATK.value = 0
            }
            ATK_p.value = atking.value ? (100 + unit_ATK_p.value) : (100 + enemy_ATK_p.value)
            ATK_f.value = atking.value ? unit_ATK_f.value : enemy_ATK_f.value
            if (ATK_f.value == '') {
                ATK_f.value = 0
            }
            skill.value = atking.value ? unit_skill.value : enemy_skill.value
            if (skill.value == '') {
                skill.value = 0
            }
            element.value = isElement() ? 125 : 100
            if (atking.value && ele_res.value && !trueDMG.value) {
                element.value = 80
            }

            cdmg.value = atking.value ? (100 + unit_cdmg.value) : (100 + enemy_cdmg.value)
            level.value = leveling()
            DEF.value = defing()
            RES.value = atking.value ? (enemy_RES.value) : (unit_RES.value)
            if (atking.value && trueDMG.value) {
                RES.value = 0
            }
            pen.value = atking.value ? (unit_pen.value) : (enemy_pen.value)
            if (RES.value == '') {
                RES.value = 0
            }
            if (pen.value == '') {
                pen.value = 0
            }
            DMG.value = Math.floor(Math.floor(Math.floor(ATK.value * ATK_p.value / 100 + ATK_f.value) 
                        * skill.value * element.value * cdmg.value / 1000000) 
                        * Math.max((100 - level.value - DEF.value - RES.value + pen.value) / 100, 0))
            console.log(ATK.value, ATK_p.value, skill.value, element.value, cdmg.value, level.value, DEF.value, RES.value, pen.value, DMG.value, ele_res.value)
            if (DMG.value == 0) {
                DMG.value = 1
            }
        }

        const exchange = (e) => {
            atking.value = !atking.value
            calculation()
            if (atking.value) {
                $(".input-def").attr("disabled", true);
                $(".defside").addClass("text-muted")
                $(".input-atk").attr("disabled", false);
                $(".atkside").removeClass("text-muted")
            }else {
                $(".input-atk").attr("disabled", true);
                $(".atkside").addClass("text-muted")
                $(".input-def").attr("disabled", false);
                $(".defside").removeClass("text-muted")
            }
        }


        onMounted(() => {
            calculation()
            $(".input-def").attr("disabled", true);
            $(".defside").addClass("text-muted")
        })

        const elementBtns = ref([false, false, false, false, false])
        const elementBtns_e = ref([false, false, false, false, false])
        const elementList = ref(['Water', 'Fire', 'Nature', 'Dark', 'Light'])

        const filter = (e) => {
            elementBtns.value = [false, false, false, false, false]
            $('.btn-filter').removeClass("active")

            elementBtns.value.forEach((item, index, array) => {
                if (e.target.closest('button').children[0].attributes["src"].value.indexOf(elementList.value[index]) >= 0) {
                    array[index] = !array[index]
                }
            })
            e.target.closest('.btn').classList.toggle("active")
            calculation()
        }
        const filter_e = (e) => {
            elementBtns_e.value = [false, false, false, false, false]
            $('.btn-filter-enemy').removeClass("active")

            elementBtns_e.value.forEach((item, index, array) => {
                if (e.target.closest('button').children[0].attributes["src"].value.indexOf(elementList.value[index]) >= 0) {
                    array[index] = !array[index]
                }
            })
            e.target.closest('.btn').classList.toggle("active")
            calculation()
        }
        
        const setDefault = () => {
            unit_ATK.value = 2000
            unit_ATK_p.value = 3
            unit_ATK_f.value = 120
            unit_skill.value = 732.05
            unit_cdmg.value = 201
            unit_level.value = 80
            unit_DEF.value = 768
            unit_RES.value = 52
            unit_pen.value = 15
            enemy_ATK.value = 5000
            enemy_ATK_p.value = 0
            enemy_ATK_f.value = 0
            enemy_skill.value = 100
            enemy_cdmg.value = 0
            enemy_level.value = 130
            enemy_DEF.value = 0
            enemy_RES.value = 15
            enemy_pen.value = 0
            trueDMG.value = true
            ele_res.value = true
            calculation()
        }

        const setZero = () => {
            unit_ATK.value = ''
            unit_ATK_p.value = ''
            unit_ATK_f.value = ''
            unit_skill.value = ''
            unit_cdmg.value = ''
            unit_level.value = ''
            unit_DEF.value = ''
            unit_RES.value = ''
            unit_pen.value = ''
            enemy_ATK.value = ''
            enemy_ATK_p.value = ''
            enemy_ATK_f.value = ''
            enemy_skill.value = ''
            enemy_cdmg.value = ''
            enemy_level.value = ''
            enemy_DEF.value = ''
            enemy_RES.value = ''
            enemy_pen.value = ''
            trueDMG.value = false
            ele_res.value = false
            calculation()
        }

        return {
            isMobile,
            text,
            language,
            Characters,
            unit_ATK,
            unit_skill,
            unit_ATK_p,
            unit_ATK_f,
            unit_cdmg,
            unit_level,
            unit_DEF,
            unit_RES,
            unit_pen,
            enemy_ATK,
            enemy_skill,
            enemy_ATK_p,
            enemy_ATK_f,
            enemy_cdmg,
            enemy_level,
            enemy_DEF,
            enemy_RES,
            enemy_pen,
            trueDMG,
            ele_res,
            ATK,
            skill,
            ATK_p,
            ATK_f,
            element,
            cdmg,
            level,
            DEF,
            RES,
            pen,
            DMG,
            filter,
            filter_e,
            setDefault,
            setZero,
            exchange,
            atking,
            calculation,
        }
    }
}
</script>

<style scoped>
table {
    opacity: 0.85;
    table-layout:fixed;
    text-align: center;
}
.btn-filter {
    width: 2em;
    border: 0;
}
.btn-filter-enemy {
    width: 2em;
    border: 0;
}
img {
    width: 100%;
}
@media screen and (max-width: 768px) {
}
</style>
